:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

* {
  box-sizing: border-box;
}

body {
  background-color: linear-gradient(#d6d6f3, bisque);
  margin: 15px;
}

.container {
  background: linear-gradient(#d6d6f3, bisque, #fff);
  padding: 30px 20px;
}

.search-form {
  width: 500px;
  height: 30px;
  box-shadow: 0px 2px 1px rgba(105, 170, 244, .08), rgba(105, 170, 244, .16), rgba(105, 170, 244, .08);
  border-bottom: 1px solid bisque;
  border-radius: 10px;
  justify-content: space-between;
  margin: 0 auto 20px;
  display: flex;
}

.is-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.img {
  display: block;
}

input {
  width: 400px;
  border-radius: 10px;
  padding-left: 10px;
}

input::-ms-input-placeholder {
  color: #a26565;
  font-style: italic;
}

input::placeholder {
  color: #a26565;
  font-style: italic;
}

input:hover, input:focus {
  color: #a26565;
  opacity: 1;
  background-color: #f6f6f4;
  border-color: #a26565;
}

button {
  min-width: 70px;
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: rgba(162, 101, 101, .5);
  border-radius: 10px;
}

.gallery {
  width: 1140px;
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: flex;
}

.photo-card {
  width: calc(25% - 11.25px);
  background-color: #fff;
  display: block;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .2), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px -1px rgba(0, 0, 0, .12);
}

.info {
  width: auto;
  height: 70px;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin-left: 5px;
  margin-right: 5px;
  padding: 10px;
  display: flex;
}

.info-item {
  letter-spacing: .01em;
  display: block;
}

.gallery-img {
  height: 170px;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.gallery-img:hover {
  transform: scale(1.03);
}

.load-more {
  margin: 20px auto;
  padding: 5px 10px;
  display: block;
}

